.sidebar{
    flex: 0.35;

}

.sidebar_header{
    display: flex;
    justify-content: space-between;
    padding: 20px;
    border-right: 1px solid lightgray;
}

.sidebar_headerRight{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 10vw;
}

.sidebar_headerRight > .MuiSvgIcon{
    margin-right: 2vw;
    font-size: 2px !important;
}

.sidebar_headerRight > .MuiSvgIcon-root {
    margin-right: 2vw;
    font-size: 20px !important;
}

.sidebar_search{
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 39px;
    padding: 10px;
}

.sidebar_searchContainer{
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px;
}

.sidebar_searchContainer > .MuiSvgIcon-root{
    color: grey;
    padding: 10px;
}

.sidebar_searchContainer > input{
    border: none;
    width: 100%;
    margin-left: 10px;
    outline-width: 0;
}

.sidebar_chats{
    display: flex;
    background-color: white;
    flex-wrap: wrap;
    overflow: hidden;
    flex-direction: column;
    inline-size: initial;
}

