.chat{
    display: flex;
    flex-direction: column;
    flex: 0.65;
}

.chat-room-name{
    margin: 0px;
}

.chat_header{
    display: flex;
    padding: 20px;
    align-items: center;
    border-bottom: 1px solid lightgray;
}

.chat_headerInfo{
    flex: 1;
    padding-left: 20px;
}

.chat_headerInfo > h3{
    margin-bottom: 3px;
    font-weight: 500;
}

.chat_headerInfo > p{
    color: grey;
}

.chat_body{
    flex: 1;
    background-image: url("https://cutewallpaper.org/21/whatsapp-wallpaper-for-chat/WhatsApp-Wallpaper-Download-Latest-Exotic-Wallpapers-.jpg");
    background-repeat: repeat;
    background-position: center;
    overflow-y: scroll;
    padding: 30px;
}

.chat_message{
    position: relative;
    font-size: 16px;
    padding: 10px;
    width: fit-content;
    border-radius: 10px;
    background-color: #ffffff;
    margin-bottom: 30px;
}

.chat_receiver{
    margin-left: auto;
    background-color: #dcf8c6;
}

.chat_timestemp{
    margin-left: 10px;
    font-size: xx-small;
    color: blue;
}

.chat_name{
    position: absolute;
    top: -15px;
    font-weight: 800;
    font-size: xx-small;
}

.chat_footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 62px;
    border-top: 1px solid lightgray;
}

.chat_footer > form{
    flex: 1;
    display: flex;
}

.chat_footer > form > input{
    flex: 1;
    border-radius: 30px;
    outline-width: 0;
    padding: 10px;
    border: none;
    margin: 0 10px 0px 10px;
}

.chat_footer > form > button{
    display: none;
}

.chat_footer > .MuiSvgIcon-root{
    padding: 11px;
    color: grey;
}